import React, { useEffect, useState } from "react";
import {
  Card,
  Col,
  Row,
  CardBody,
  CardTitle,
  Table,
  Button,
  Label,
  Spinner,
} from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllMerchants,
  getAllUsers,
  getMerchantManagedByAdmin,
  updateMerchant,
} from "../../redux/actions";
import { COLORS } from "../../colors";
import AvatarImage from "../../assets/images2/avatar.png";
import MerchantForm from "./merchantForm";
// import { log } from "util";
import Pagination from "../../components/Pagination/Pagination";
import { imgURL } from "../../redux/config";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { toast } from "react-toastify";

const Vendor = () => {
  const dispatch = useDispatch();
  const [userList, setuserList] = useState([]);
  const customerData = useSelector((state) => state.user.customer);
  const [selectedDetails, setselectedDetails] = useState(null);
  const [editDetails, setEditDetails] = useState(null);
  const [merchantStatus, setMerchantStatus] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [recordsPerPage] = useState(15);



  const merchantManagedByAdminSelector = useSelector(
    (state) => state.getMerchantManagedByAdmin.data.data
  );

  const allMerchants = useSelector(
    (state) => state.allMerchants.data.data
  );
  const state = useSelector(state => state)

  // console.log(state)

  // console.log("item", merchantManagedByAdminSelector);

  const indexOfLastRecord = currentPage * recordsPerPage;
  const indexOfFirstRecord = indexOfLastRecord - recordsPerPage;
  // const currentRecords = merchantManagedByAdminSelector.slice(indexOfFirstRecord, indexOfLastRecord);
  const currentRecords = allMerchants.slice(indexOfFirstRecord, indexOfLastRecord);
  const nPages = Math.ceil(allMerchants.length / recordsPerPage)




  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };


  useEffect(() => {

    if (!customerData.length) {
      dispatch(getAllUsers());
    } else {
      if (customerData.length !== userList.length) {
        setuserList(customerData);
      }

    }


  }, [customerData.length]);



  useEffect(() => {
    // dispatch(getMerchantManagedByAdmin(() => { }));
    dispatch(getAllMerchants(() => { }))
  }, []);




  const updateStatus = (item) => {
    let obj = {
      // name: item.profile.name,
      userStatus: item.userStatus === "active" ? "disable" : "active",
    };
    // console.log("id user merchant: ", item._id);
    dispatch(updateMerchant(item._id, obj, (data) => { setMerchantStatus(data); toast.success(`Merchant  ${item.userStatus == "active" ? "Disabled" : "Activated"}`) }));

  };

  const userProfileDetails = (data) => (
    <>
      {/* <CardBody className=""> */}

      {/*{console.log("userProfileDetails-------", data)}*/}
      <div className={"m-5"}>
        <h3 className={"font-weight-bold"}>Merchant Details</h3>
      </div>
      <div
        className="row"
        style={
          {
            // border:'1px solid red'
          }
        }
      >
        <div className="col-md-4">
          <div
            className="profile-img"
            style={{
              marginRight: "40px",
              // border:'1px solid red'
            }}
          >
            {/* <Slider {...settings}>
              {data.image.map((i, index) => (
                <div key={index}
                style={{
                  height:'400px' ,
                  border:'1px solid red' }}
                >
                  <img
                    src={i ? imgURL + i : AvatarImage}
                    style={{ 
                      width: '400px', 
                      height: '100%',
                      backgroundRepeat:'no-repeat',
                    backgroundSize:'cover',
                verticalAlign:'middle',
                      // backgroundPosition:'centre',
                      objectFit:'cover'
                    // border:'1px solid red'
                   }}
                      alt={index}
                  />
                </div>
              ))}
            </Slider> */}
          </div>
        </div>
        <div className="col-md-8">
          <div className="profile-head">
            <h5 className={"font-weight-bold"}>
              {selectedDetails.profile.name}
            </h5>
            {/*<h6>email</h6>*/}
            <ul className="nav nav-tabs" id="myTab" role="tablist">
              <li className="nav-item">
                <a
                  className="nav-link active"
                  id="home-tab"
                  data-toggle="tab"
                  href="#home"
                  role="tab"
                  aria-controls="home"
                  aria-selected="true"
                >
                  About
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="row">
        <Slider {...settings}>
          {data.image.map((i, index) => (
            <div
              key={index}
              style={{
                height: "400px",
                border: "1px solid red",
              }}
            >
              <img
                src={i ? imgURL + i : AvatarImage}
                style={{
                  width: "440px",
                  height: "100%",
                  padding: '10px 30px'
                }}
                alt={index}
              />
            </div>
          ))}
        </Slider>
        <div className="col-md-4">
          <div className="tab-content profile-tab" id="myTabContent">
            <div
              className="tab-pane fade show active"
              id="home"
              role="tabpanel"
              aria-labelledby="home-tab"
            >
              <Row>
                <Col md={6}>
                  <br /> <Label>Status</Label>
                  <br /> <Label>Name</Label>
                  <br /> <Label>Email</Label>
                  <br /> <Label>Phone</Label>
                  <br /> <Label>Business Name</Label>
                  <br /> <Label>Website</Label>
                </Col>
                <Col md={6} style={{ marginTop: "20px" }}>
                  <Label>{selectedDetails.userStatus}</Label>
                  <br />
                  <Label>{selectedDetails.profile.name}</Label>
                  <br />
                  <Label>{selectedDetails.email} </Label>
                  <br />
                  <Label>{selectedDetails.phone}</Label>
                  <br />
                  <Label>{selectedDetails.profile.businessName}</Label>
                  <br />
                  <Label>{selectedDetails.profile.website}</Label>
                </Col>
              </Row>
            </div>
            <div
              className="tab-pane fade"
              id="profile"
              role="tabpanel"
              aria-labelledby="profile-tab"
            >
              <div className="row">
                <div className="col-md-6">
                  <Label>Experience</Label>
                </div>
                <div className="col-md-6">
                  <p>Expert</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-6">
                  <Label>Hourly Rate</Label>
                </div>
                <div className="col-md-6">
                  <p>10$/hr</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-6">
                  <Label>Total Projects</Label>
                </div>
                <div className="col-md-6">
                  <p>230</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-6">
                  <Label>English Level</Label>
                </div>
                <div className="col-md-6">
                  <p>Expert</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-6">
                  <Label>Availability</Label>
                </div>
                <div className="col-md-6">
                  <p>6 months</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <Label>Your Bio</Label>
                  <br />
                  <p>Your detail description</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );

  const renderSelectedUser = () => {

    let data = selectedDetails.profile;

    return (
      <>
        <Card>
          <CardTitle
            className="border-bottom p-3 mb-0"
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
            }}
          >
            <div>
              <i className="mdi mdi-border-none mr-2"> </i>
              {data.name} | Merchant Details
            </div>
            <Button
              className="btn btn-primary"
              style={{
                backgroundColor: COLORS.black,
                borderColor: COLORS.black,
                borderRadius: 4,
                alignItems: "center",
              }}
              onClick={() => setselectedDetails(null)}
            >
              <i className="fa fa-arrow-left mr-2"> </i>Back
            </Button>
          </CardTitle>
          <CardBody>{userProfileDetails(data)}</CardBody>
        </Card>
      </>
    );
  };

  // console.log(
  //   " merchantManagedByAdminSelector=============",
  //   merchantManagedByAdminSelector
  // );



  const renderUserList = () => (
    <>
      <Card>
        <CardTitle className="bg-light border-bottom p-3 mb-0">
          <i className="mdi mdi-border-none mr-2"> </i>
          Merchants
        </CardTitle>


        <CardBody className="" style={{ fontSize: 12 }}>
          <Table
            striped
            hover
            responsive
            condensed
            search={true}
            deleteRow={true}
            pagination
            tableHeaderClass="mb-0"
          >
            <thead>
              <tr style={{ textAlign: "center" }}>
                <th>Sr No#</th>
                <th>Photo</th>
                <th>Name</th>
                <th>Email</th>
                <th>Business Name</th>
                <th>Phone</th>
                <th>Status</th>
                <th>Managed by Admin</th>
                <th colSpan={2}>Action</th>
              </tr>
            </thead>


            <tbody className="responsive">
              {allMerchants
                ? currentRecords.map((item, index) => (
                  <tr key={index} style={{ textAlign: "center", height: 60 }}>
                    <th scope="row">{index + 1}</th>
                    <td>
                      <img
                        src={
                          item.picture
                            ? imgURL + item.picture
                            : AvatarImage
                        }
                        style={{
                          width: 50,
                          height: 40,
                          marginTop: -5,
                          resize: "contain",
                        }}
                        alt={item.title}
                      />
                    </td>

                    <td>{item.name}</td>
                    <td>{item.email}</td>
                    <td>
                      {item.businessName}
                    </td>
                    <td>{item.phone}</td>
                    <td>{item.userStatus}</td>
                    <td>{item.managedByAdmin ? "True" : "False"}</td>
                    <td>

                      <Button
                        className="btn btn-info"
                        style={{
                          fontSize: 12,
                          backgroundColor:
                            item.userStatus === "active"
                              ? COLORS.primary
                              : COLORS.black,
                          borderColor:
                            item.userStatus === "active"
                              ? COLORS.primary
                              : COLORS.black,
                          width: "80px",
                        }}
                        onClick={() => updateStatus(item)}
                      >
                        {item.userStatus === "active" ? "Disable" : "Active"}
                      </Button>


                      <Button
                        onClick={() => {
                          setselectedDetails(item);
                        }}
                        style={{
                          width: "60px",
                          fontSize: 12,
                          backgroundColor: COLORS.black06,
                          borderColor: COLORS.black06,
                        }}
                        className="btn btn-info ml-3"
                      >
                        View
                      </Button>


                      <Button
                        disabled={!item.managedByAdmin}
                        onClick={() => {
                          setEditDetails(item);
                          console.log(item)
                        }}
                        style={{
                          width: "60px",
                          fontSize: 12,
                        }}
                        className="btn btn-primary ml-3"
                      >
                        Edit
                      </Button>
                    </td>
                  </tr>
                ))
                : <Spinner animation="border" variant="success" />}
            </tbody>


          </Table>

        </CardBody>


      </Card>


      <Pagination
        nPages={nPages}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        
      />
    </>
  );


  return selectedDetails ? (
    renderSelectedUser()

    
  ) : (
    <>
    
      <MerchantForm
        editData={editDetails}
        cancelEdit={() => setEditDetails(null)}
        updateList={(data) => setMerchantStatus(data)}

      />
      {renderUserList()}
    </>
  );
};

export default Vendor;